/* global console, document, Excel, Office */

// 673b87984c1849.71141183

import axios from "axios";

const buySellColumns = ["ISIN", "Symbol", "Buy-Sell", "Quantity", "DD", "MM", "YYYY"];
const portofolioColumns = ["ISIN", "Symbol", "Quantity", "Date", "Net Amount", "Effective Unit Price"];

async function getStockPrice(date, symbol, ticker = "NSE") {
  try {
    let d = date.split("/"); //.reverse();
    if (d[1].length === 1) {
      d[1] = "0" + d[1];
    }
    if (d[2].length === 1) {
      d[2] = "0" + d[2];
    }
    d = d.join("-");
    console.log(d);
    const data = await axios.get(
      `https://eodhd.com/api/eod/${symbol}.${ticker}?from=${d}&to=${d}&api_token=673b87984c1849.71141183&fmt=json`
    );

    return data["data"][0]["close"];
  } catch (err) {
    console.log(err.message);
  }
}

async function deleteSheet() {
  try {
    await Excel.run(async (context) => {
      try {
        let sheets = context.workbook.worksheets;
        sheets.load("items/name");
        await context.sync();

        let sheet = sheets.items.filter((sheet) => sheet.name === "Portfolio")[0];
        sheet.delete();
        await context.sync();
      } catch (err) {
        console.log(err.message);
      }
    });
  } catch (err) {
    console.log(err.message);
  }
}

async function createSheet() {
  try {
    await Excel.run(async (context) => {
      let buySellSheet = context.workbook.worksheets.getItem("Buy-Sell Entry");
      let portfolioSheet = context.workbook.worksheets.getItem("Portfolio Template");
      let copiedSheet = portfolioSheet.copy(Excel.WorksheetPositionType.after, buySellSheet);
      copiedSheet.name = "Portfolio";
      await context.sync();
    });
  } catch (err) {
    console.log(err.message);
  }
}

async function updateHoldings(context, ws, holding, realized) {
  try {
    const cachedPrices = {}; // To avoid duplicate API calls
    const cagrValues = []; // To store individual CAGRs for Portfolio RMS calculation

    // Helper function to fetch and cache stock prices
    async function fetchStockPrice(date, symbol) {
      if (cachedPrices[symbol + date]) return cachedPrices[symbol + date];
      const price = await getStockPrice(date, symbol);
      cachedPrices[symbol + date] = price || 0;
      return price;
    }

    // Process Holdings
    const holdingData = [];
    for (let isin in holding) {
      const stock = holding[isin];
      let totalCost = 0;
      let quantity = 0;
      let startDate = null;

      for (let t of stock) {
        quantity += t[2];
        totalCost += t[5] * t[2];
        if (!startDate || new Date(t[3]) < new Date(startDate)) {
          startDate = t[3]; // Get the earliest purchase date
        }
      }

      const currentPrice = await fetchStockPrice(startDate, stock[0][1]);
      const cagr = Math.pow(currentPrice / (totalCost / quantity), 1 / (1 / 365)) - 1;
      cagrValues.push(cagr);

      holdingData.push([
        isin,
        stock[0][1], // Symbol
        quantity,
        startDate,
        totalCost,
        cagr.toFixed(4),
      ]);
    }

    // Calculate RMS Portfolio CAGR
    const rmsCAGR = Math.sqrt(cagrValues.reduce((sum, cagr) => sum + cagr ** 2, 0) / cagrValues.length);

    // Update Portfolio Sheet
    const range = ws.getRange(`A4:F${holdingData.length + 3}`);
    range.values = holdingData;

    // Display RMS Portfolio CAGR at the top
    const cagrRange = ws.getRange("B1");
    cagrRange.values = [["Portfolio CAGR (RMS): " + rmsCAGR.toFixed(4) + "%"]];
  } catch (err) {
    console.error("Error in updateHoldings:", err.message);
  }
}

async function run() {
  try {
    await Excel.run(async (context) => {
      await deleteSheet();
      await createSheet();

      const buySellSheet = context.workbook.worksheets.getItem("Buy-Sell Entry");
      const portfolioSheet = context.workbook.worksheets.getItem("Portfolio");

      const buySellRange = buySellSheet.getRange("B3:H100");
      const priceRange = buySellSheet.getRange("M3:N100");
      buySellRange.load("values");
      priceRange.load("values");

      await context.sync();

      let holding = {};
      let realized = [];

      for (let row of buySellRange.values) {
        const [ISIN, symbol, type, quantity, dd, mm, yyyy, price] = row;
        const date = `${dd}/${mm}/${yyyy}`;

        if (type === "B") {
          if (!holding[ISIN]) holding[ISIN] = [];
          holding[ISIN].push([ISIN, symbol, quantity, date, price]);
        } else if (type === "S") {
          let remaining = quantity;

          while (remaining > 0 && holding[ISIN]?.length > 0) {
            const buyEntry = holding[ISIN][0];
            const sellQuantity = Math.min(remaining, buyEntry[2]);
            realized.push([
              ISIN,
              symbol,
              sellQuantity,
              buyEntry[3],
              date,
              sellQuantity * price,
              sellQuantity * buyEntry[4],
            ]);

            buyEntry[2] -= sellQuantity;
            remaining -= sellQuantity;

            if (buyEntry[2] === 0) holding[ISIN].shift();
          }
        }
      }

      await updateHoldings(context, portfolioSheet, holding, realized);
    });
  } catch (error) {
    console.error("Error in run:", error.message);
  }
}
async function main() {
  try {
    const info = await Office.onReady();
    if (info.host === "Excel") {
      document.getElementById("btn").onclick = run;
    }
  } catch (err) {
    console.log(err.message);
  }
}

main();
